*, :after, :before {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (width >= 400px) {
  .container {
    width: 400px;
  }
}

@media screen and (width >= 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (width >= 1200px) {
  .container {
    width: 1200px;
  }
}

.title {
  text-align: center;
  font-size: 24px;
}

@media screen and (width >= 768px) {
  .title {
    font-size: 32px;
  }
}

@media screen and (width >= 1200px) {
  .title {
    font-size: 46px;
  }
}

body {
  background-color: #dad7d7;
}

.search__form {
  width: 400px;
  margin: 10px auto 0;
  display: block;
}

input {
  border: none;
  border-radius: 10px;
  width: 400px;
  height: 50px;
  padding-left: 15px;
}

input:hover, input:focus {
  outline: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 10px;
  display: flex;
}

.list__item {
  background-color: #e6e6e6;
  flex-basis: 100%;
  transition: all .5s;
}

.list__item:hover {
  transform: scale(1.1);
}

@media screen and (width >= 768px) {
  .list__item {
    flex-basis: calc(33.3333% - 13.3333px);
  }
}

@media screen and (width >= 1200px) {
  .list__item {
    flex-basis: calc(25% - 15px);
  }
}

.stats {
  justify-content: center;
  align-items: center;
  gap: 6px;
  display: flex;
}

.stats-item {
  flex-direction: column;
  align-items: center;
  gap: 6px;
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
}

.load__btn {
  color: #1c1c1c;
  background-color: #dad7d7;
  border: none;
  border-radius: 20px;
  margin: 10px auto 0;
  padding: 10px 30%;
  transition: all 1s;
  display: none;
}

.load__btn:hover {
  color: #393939;
  background-color: #acabab;
}
